import React, { useState, useEffect } from 'react';
import Layout from '../components/layout/layout'
import { Link, useNavigate } from 'react-router-dom';
import Loader from '../components/loader';
import { message } from 'antd';

function Login() {

    const navigate = useNavigate();

    function validateGmail(mail) {
        const pattern = /^[A-Za-z0-9](?:[A-Za-z0-9._-]{5,29})@gmail\.com$/;
        if (pattern.test(mail)) {
            return true; // Valid 
        } else {
            return false; // Invalid 
        }
    }

    const [email, setMail] = useState('');
    const [password, setPassword] = useState('');
    const [load, setLoad] = useState(false)

    useEffect(() => {
        const auth = localStorage.getItem('alnasar');
        if (auth) {
            navigate('/home')
        }
    })


    async function submission() {
        setLoad(true)
        try {
            if (validateGmail(email)) {

                // Create an object with the data to send
                const userData = {
                    email,
                    password,
                };

                const response = await fetch('/api/v1/login', {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json',
                    },
                    body: JSON.stringify(userData),
                });

                const data = await response.json();
                // console.log("From Login page", data);
                if (response.status === 200) {
                    const { name } = data.dataToSend; // Destructure the data object
                    const { token } = data;
                    const resultObject = { name, token }; // Create an object with _id, name, and token
                    // console.log("RESULT TO STORE IN MEMORY: ", resultObject);
                    if (resultObject) {
                        localStorage.setItem('alnasar', JSON.stringify(resultObject));
                    }
                    else {
                        message.error("Error in login. Contact for help")
                    }
                }


                else if (response.status === 401) {
                    message.warning("Credentials didn't match");
                    setLoad(false)
                }
                else {
                    message.warning("User isn't registered!")
                    navigate('/signup')
                }
            }
            else {
                message.error('Not a valid Gmail')
            }
        } catch (error) {
            // Handle any errors that occur during the request
            // console.error('Error:', error);
        }
        finally {
            setLoad(false);
        }
    }


    return (

        <Layout>
            <div className='login' style={{ border: '1px dashed darkblue' }}>
                {load ? <Loader /> : <>
                    <h1>Login</h1>
                    <p style={{ textAlign: 'center', padding: '0.3rem 2rem' }}>Emails with gmail.com are valid only</p>
                    <form>
                        <div className="form-floating mb-3">
                            <input
                                type="email"
                                className="form-control"
                                id="floatingInput"
                                name="email"
                                placeholder="name@example.com"
                                value={email}
                                onChange={(e) => { setMail(e.target.value) }}
                            />
                            <label htmlFor="floatingInput">Email address</label>
                        </div>
                        <div className="form-floating">
                            <input
                                type="password"
                                className="form-control"
                                id="floatingPassword"
                                name="password"
                                placeholder="Password"
                                value={password}
                                onChange={(e) => { setPassword(e.target.value) }}
                            />
                            <label htmlFor="floatingPassword">Password</label>
                        </div>
                        <br />
                        <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
                            <p>No Account? <Link to={'/signup'}>SignUP</Link></p>
                            <br />
                            <button onClick={submission}>Login</button>
                        </div>
                    </form>
                </>}
            </div>



        </Layout>
    )
}

export default Login
